import React, { Component } from "react";
import Slider from "react-slick";
import blog1 from "../../image/blog-1.jpg";
import blog2 from "../../image/blog-2.jpg";
import blog3 from "../../image/blog-3.jpg";
import blog4 from "../../image/blog-4.jpg";
import blog5 from "../../image/blog-5.jpg";
import blog6 from "../../image/blog-6.jpg";

class Blog extends Component {
  constructor(props) {
    super(props);
    this.state = [
      {
        title: "2 Cara Uninstall Microsoft Office Semua Versi",
        url:
          "http://smartnetid.blogspot.com/2019/03/cara-uninstall-microsoft-office-semua-versi.html",
        desc:
          "Ketika kita ingin mengupgrade Office ke versi terbaru alangkah baiknya menghapus Office lama. Hal ini agar memory penyimpanan tidak terlalu ...",
        image: blog1,
        comment: "5",
        date: "March 17, 2019",
      },
      {
        title: "Cara Membersihkan Keyboard Laptop dan PC dengan Mudah",
        url:
          "http://smartnetid.blogspot.com/2019/02/cara-membersihkan-keyboard-laptop-pc.html",
        desc:
          "Keyboard merupakan komponen hardware yang mudah sekali terkena debu karena berada di luar CPU. Dibandingkan dengan mouse...",
        image: blog2,
        comment: "4",
        date: "February 9, 2019",
      },
      {
        title: "10 Package Atom Terbaik yang Wajib Kamu Install",
        url:
          "http://smartnetid.blogspot.com/2019/02/package-atom-terbaik-wajib-kamu-install.html",
        desc:
          "Penulisan bahasa programan kini telah dimudahkan dengan adanya text editor. Banyak sekali text editor yang telah tersedia di internet sekara...",
        image: blog3,
        comment: "2",
        date: "February 5, 2019",
      },
      {
        title: "16 Text Editor PC Terbaik Untuk Pemula 2019",
        url:
          "http://smartnetid.blogspot.com/2019/02/text-editor-pc-untuk-pemula-terbaik.html",
        desc:
          "Setiap tahunnya perkembangan teknologi selalu berkembang. Perkembangan tersebut dapat kita rasakan dari segi hardware maupun software. Nah...",
        image: blog4,
        comment: "5",
        date: "February 3, 2019",
      },
      {
        title: "Cara Mengatasi Keyboard Laptop Eror atau Tidak Berfungsi",
        url:
          "http://smartnetid.blogspot.com/2018/12/cara-mengatasi-keyboard-laptop-eror.html",
        desc:
          "Sebuah laptop dirancang dengan memiliki fitur buitl-in. Artinya, semua komponen pada laptop sudah terpasang atau lengket pada laptop itu sen...",
        image: blog5,
        comment: "6",
        date: "December 23, 2018",
      },
      {
        title: "20 Tips Membeli Laptop Bekas Berkualitas dan Bagus",
        url:
          "http://smartnetid.blogspot.com/2018/12/20-tips-membeli-laptop-bekas.html",
        desc:
          "Laptop sudah menjadi bawaan setiap orang pada umumnya. Laptop yang kerjanya sama dengan komputer ini dapat dibawa dan digunakan dimana saja....",
        image: blog6,
        comment: "4",
        date: "December 22, 2018",
      },
    ];
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
          },
        },
        {
          breakpoint: 992,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
          },
        },
        {
          breakpoint: 770,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
          },
        },
      ],
    };

    const blogs = this.state.map((blog, index) => {
      return (
        <div className="col-md">
          <div className="card" key={index}>
            <a href={blog.url}>
              <img src={blog.image} className="card-img-top" />
            </a>
            <div className="card-body">
              <h5 className="card-title">
                <a href={blog.url}>{blog.title}</a>
              </h5>
              <p className="card-text">{blog.desc}</p>
              <a href={blog.url} className="card-link">
                <i class="fas fa-comment-alt"></i> {blog.comment}
              </a>
              <a href={blog.url} className="card-link float-right">
                <i class="fas fa-calendar-alt"></i> {blog.date}
              </a>
            </div>
          </div>
        </div>
      );
    });
    return (
      <section className="blog">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md">
              <div className="header mb-3">
                <h5 className="text-muted">Recent Blog</h5>
                <h3>Read My Latest Article</h3>
              </div>
              <div className="body">
                <Slider ref={(c) => (this.slider = c)} {...settings}>
                  {blogs}
                </Slider>
                <div style={{ textAlign: "center" }} className="mt-5">
                  <button className="btn-react mr-2" onClick={this.previous}>
                    Prev
                  </button>
                  <button className="btn-react" onClick={this.next}>
                    Next
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Blog;
