import React, { Component } from "react";
import { Jumbotron, Container } from "react-bootstrap";

class JumboTron extends Component {
  render() {
    return (
      <Jumbotron fluid>
        <Container>
          <div className="display-jumbo">
            <h5 className="text-muted mb-3">HEI THERE !</h5>
            <h1>I AM VARREL TANTIO</h1>
            <p>Web Development | App Development</p>
            <a href="" className="btn-react mt-5">
              My Project
            </a>
          </div>
        </Container>
      </Jumbotron>
    );
  }
}

export default JumboTron;
