import React, { Component } from "react";
import info1 from "../../image/info-1.jpg";
import info2 from "../../image/info-2.jpg";
import info3 from "../../image/info-3.jpg";

class InfoPanel extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-10 info-panel">
              <div className="row text-center">
                <div className="col-lg">
                  <img src={info1} className="" alt="" />
                  <div className="desc">
                    <h4>1.5K+</h4>
                    <p>Client Reviews</p>
                  </div>
                </div>

                <div className="col-lg">
                  <img src={info2} className="" alt="" />
                  <div className="desc">
                    <h4>9/10</h4>
                    <p>Average Rating</p>
                  </div>
                </div>

                <div className="col-lg">
                  <img src={info3} className="" alt="" />
                  <div className="desc">
                    <h4>100+</h4>
                    <p>Project Completed</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default InfoPanel;
