import React, { Component } from "react";
import { Container } from "react-bootstrap";
import about from "../../image/vector-web.jpg";

class About extends Component {
  render() {
    return (
      <section className="about">
        <Container>
          <div className="row justify-content-center mt-5">
            <div className="col-lg-4">
              <img src={about} className="img-fluid w-100" />
            </div>
            <div className="col-lg-6 text-about">
              <h5 className="text-muted">About Me</h5>
              <h3 className="mb-3">Varrel Dwitantio Purwadiansyah</h3>
              <p>
                I am passionate about making web and applications. I have worked
                on several projects and created a blog about the web and
                applications development.
              </p>
              <a href="" className="btn-react mt-3 mb-3">
                Download CV
              </a>
            </div>
          </div>
        </Container>
      </section>
    );
  }
}

export default About;
