import React, { Component } from "react";
import project1 from "../../image/project-1.jpg";
import project2 from "../../image/project-2.jpg";
import project3 from "../../image/project-3.jpg";
import project4 from "../../image/project-4.jpg";
import project5 from "../../image/project-5.jpg";

class Project extends Component {
  render() {
    return (
      <section className="project">
        <div className="container">
          <div className="row mb-3 header">
            <div className="col-sm-6">
              <h5 className="text-muted">My Project</h5>
              <h3>My Latest Collection of Projects</h3>
            </div>
          </div>

          <div className="row body">
            <div className="col-lg-6 col-md-6">
              <div className="my-project">
                <img src={project1} className="img-fluid w-100" alt="" />
                <div className="overlay"></div>
                <div className="info">
                  <h4>Web Development</h4>
                  <p>Html, CSS, PHP, JavaScript</p>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-md-6">
              <div className="my-project">
                <img src={project2} className="img-fluid w-100" alt="" />
                <div className="overlay"></div>
                <div className="info">
                  <h4>Web Development</h4>
                  <p>Html, CSS, JavaScript</p>
                </div>
              </div>
            </div>
            <div className="col-lg-3 col-md-6">
              <div className="my-project">
                <img src={project3} className="img-fluid w-100" alt="" />
                <div className="overlay"></div>
                <div className="info">
                  <h4>Web Development</h4>
                  <p>Html, CSS, Ajax</p>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-md-6">
              <div className="my-project">
                <img src={project4} className="img-fluid w-100" alt="" />
                <div className="overlay"></div>
                <div className="info">
                  <h4>Web Development</h4>
                  <p>Html, CSS, PHP, JavaScript</p>
                </div>
              </div>
            </div>
            <div className="col-lg-3 col-md-6">
              <div className="my-project">
                <img src={project5} className="img-fluid w-100" alt="" />
                <div className="overlay"></div>
                <div className="info">
                  <h4>Web Development</h4>
                  <p>Html, Css, JavaScript</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Project;
