import React, { Component } from "react";
import client1 from "../../image/img1.png";
import client2 from "../../image/img2.png";
import client3 from "../../image/img3.png";

class Testimonial extends Component {
  constructor() {
    super();
    this.state = {
      todos: [
        {
          nama: "Rohman",
          image: client1,
          review:
            "Pelayanan yang sangat baik dan cepat respon, the best web development in the world",
        },
        {
          nama: "David",
          image: client2,
          review:
            "Banyak pilihan web yang sangat responsive dan tentunya cepat meskipun memuat banyak data",
        },
        {
          nama: "Jajang",
          image: client3,
          review:
            "Meskipun masih sedikit App Mobile, tetapi menu yang ditampilkan sangat menarik",
        },
      ],
      currentPage: 1,
      todosPerPage: 1,
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(event) {
    this.setState({
      currentPage: Number(event.target.id),
    });
  }

  render() {
    const { todos, currentPage, todosPerPage } = this.state;

    // Logic for displaying todos
    const indexOfLastTodo = currentPage * todosPerPage;
    const indexOfFirstTodo = indexOfLastTodo - todosPerPage;
    const currentTodos = todos.slice(indexOfFirstTodo, indexOfLastTodo);

    const renderTodos = currentTodos.map((todo, index) => {
      return (
        <div key={index} className="testi-client">
          <img src={todo.image} className="rounded-circle img-fluid" />
          <h6 className="mt-2">{todo.nama}</h6>
          <h5>
            <i>"{todo.review}"</i>
          </h5>
        </div>
      );
    });

    // Logic for displaying page numbers
    const pageNumbers = [];
    for (let i = 1; i <= Math.ceil(todos.length / todosPerPage); i++) {
      pageNumbers.push(i);
    }

    const renderPageNumbers = pageNumbers.map((number) => {
      return (
        <button
          key={number}
          id={number}
          className="btn num-page ml-2 mr-2 mt-3"
          onClick={this.handleClick}
        ></button>
      );
    });

    return (
      <React.Fragment>
        <section className="testimonial">
          <div className="container">
            <div className="row">
              <div className="col-sm mt-5 text-white">
                <h3 className="text-center">Client Reviews</h3>
              </div>
            </div>
            <div className="row justify-content-center">
              <div className="col-md-4 text-white text-center">
                {renderTodos}
                {renderPageNumbers}
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default Testimonial;
