import React, { Component } from "react";
import html from "../image/html.png";
import css from "../image/css.png";
import js from "../image/js.png";
import php from "../image/php.png";
import brand from "../image/brand.png";

class Footer extends Component {
  render() {
    const image = [html, css, js, php];
    return (
      <React.Fragment>
        <section className="skill">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-md-12 text-center">
                {image.map((skill, index) => {
                  return (
                    <img src={skill} key={index} className="img-fluid mr-5" />
                  );
                })}
              </div>
            </div>
          </div>
        </section>
        <footer>
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-md-4">
                <h3 className="brand">
                  {" "}
                  <img alt="" src={brand} width="60" height="40" /> Varrel
                  Tantio
                </h3>
                <p>
                  Copyright 2020 All rights reserved | This template is made
                  with by <a href="">Varrel Tantio</a>
                </p>
              </div>
              <div className="col-md-3 ml-auto">
                <h3>Explore</h3>
                <ul className="list-unstyled">
                  <li>
                    <a className="text-decoration-none" href="">
                      Home
                    </a>
                  </li>
                  <li>
                    <a className="text-decoration-none" href="">
                      Blog
                    </a>
                  </li>
                  <li>
                    <a className="text-decoration-none" href="">
                      Project
                    </a>
                  </li>
                  <li>
                    <a className="text-decoration-none" href="">
                      Contact
                    </a>
                  </li>
                </ul>
              </div>
              <div className="col-md-3 ml-auto">
                <h3>Follow</h3>
                <ul className="list-unstyled">
                  <li>
                    <a className="text-decoration-none" href="">
                      Instragam
                    </a>
                  </li>
                  <li>
                    <a className="text-decoration-none" href="">
                      Facebook
                    </a>
                  </li>
                  <li>
                    <a className="text-decoration-none" href="">
                      Youtube
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </footer>
      </React.Fragment>
    );
  }
}

export default Footer;
